import app from '@/common/app.js';
import loadcache from '@/common/loadcache.js';
import request from '@/common/request.js';
export default {
    data() {
        return {
            id: '',
            name: '',
            position: '',
            infoval: '',
            hasDelete: false,
            typeArr: ['请选择'],
            typeIndex: 0,
            type: '',
            saveFlag: false,
            deleteFlag: false
        }
    },
    onLoad(event) {
        this.typeArr = loadcache.getDictsByTypeCode(app.DICT_TYPECODE.POLITICS_TYPE);
        const payload = event.id || event.payload;
        if (payload != null) {
            try {
                this.id = JSON.parse(decodeURIComponent(payload));
            } catch (error) {
                this.id = JSON.parse(payload);
            };

            this.hasDelete = true;
        }

    },
    onShow() {
        if (this.id != '') {
            this.getSocial();
        }
    },
    methods: {
        getSocial(e) {
            request.http({
                url: '/app/social/getOne',
                header: 'application/x-www-form-urlencoded',
                method: "POST",
                data: {
                    id: this.id
                },
                success: (res) => {
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        this.name = res.data.data.name;
                        this.position = res.data.data.position;
                        this.infoval = res.data.data.infoval;
                        this.type = res.data.data.status;
                        this.typeArr.forEach((d, index) => {
                            if (d.value == this.type) {
                                this.typeIndex = index;
                            }
                        });
                    }
                },
                error: (res) => {}
            });
        },
        bindPickerChangeType(e) {
            this.typeIndex = e.target.value;
        },
        saveSocial(e) {
            
            this.saveFlag = true;
            this.type = this.typeArr[this.typeIndex].value;
            if (this.name == '' || this.name == null) {
                this.saveFlag = false;
                this.showpageToast("请输入名称");
                return false;
            }
            if (this.position == '' || this.position == null) {
                this.saveFlag = false;
                this.showpageToast("请输入职务");
                return false;
            }
            request.http({
                url: '/app/social/saveSocial',
                header: 'application/x-www-form-urlencoded',
                method: "POST",
                data: {
                    id: this.id,
                    name: this.name,
                    status: this.type,
                    position: this.position,
                    infoval: this.infoval,
                },
                success: (res) => {
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        uni.navigateBack({

                        });
                        this.showpageToast(res.data.msg);
                        this.saveFlag = false;
                    }
                },
                error: (res) => {
                    this.saveFlag = false;
                }
            });
        },
        deleteSocial(e) {
            
            this.deleteFlag = true;
            uni.showModal({
                title: '',
                content: '确定删除该社会兼职？',
                success: (res) => {
                    if (res.confirm) {
                        request.http({
                            url: '/app/social/deleteSocial',
                            header: 'application/x-www-form-urlencoded',
                            method: "POST",
                            data: {
                                id: this.id
                            },
                            success: (res) => {
                                if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                                    uni.navigateBack({

                                    });
                                    this.showpageToast(res.data.msg);
                                    this.deleteFlag = false;
                                }
                            },
                            error: (res) => {
                                this.deleteFlag = false;
                            }
                        });
                    }else{
                        this.deleteFlag =false;
                    }
                }
            });
        },
        showpageToast(msg) {
            uni.showToast({
                icon: 'none',
                title: msg
            });
        },
    }
}
